.footer {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

.footer__box {
  &--top {
    padding-bottom: 47px;
    background-color: $bg-dark-secondary;
  }

  &--bottom {
    padding-bottom: 16px;
    background-color: $bg-dark-first;
  }
}

.footer__inner {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}


.footer__contacts {
  margin-top: 30px;
  order: 2;
}

.footer__nav-pages {
  margin-top: 35px;
  order: 1;
}

.footer__nav-category {
  margin-top: 30px;
  order: 3;
}

.footer__address, .footer__phone {
  margin: 0;
  font-size: 18px;
  line-height: 24px;
  color: $color-white; 
}

.footer__copyright {
  flex-basis: 265px;
  margin-top: 40px;
  font-size: 16px;
  line-height: 18px;
  color: $color-white;
}

.footer__social {
  margin-top: 40px;
}

.footer__text-wrap {
  flex-basis: 280px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-right: 15px;
}

.footer__text {
  margin-top: 40px;
}

.footer__text, .footer__link {
  font-size: 16px;
  line-height: 18px;
  color: $color-white;
}

.footer__link {
  color: $color-accent;
  outline: none;
  transition: all 0.3s;

  &:hover, &:focus {
    text-decoration: underline;
  }

  &:active {
    color: $color-red;
  }
}

@media(max-width: $tablet-width){
  .footer__inner {
    align-items: center;
  }

  .footer__contacts {
    width: 100%;
    max-width: 170px;
  }

  .footer__nav-pages, .footer__nav-category {
    flex-basis: 70%;
  }

  .footer__box--bottom {
    padding: 40px 0 32px;
  }

  .footer__box--bottom .footer__inner {
    justify-content: space-between;
    align-items: center;
  }

  .footer__copyright {
    flex-basis: 35%;
    margin: 0;
  }

  .footer__social {
    margin: 0;
  }

  .footer__text-wrap {
    flex-basis: 35%;
  }

  .footer__text {
    margin: 0;
  }
}

@media(max-width: $mobile-width){
  .footer__inner {
    display: block;
  }

  .footer__box {
    &--top {
      padding-bottom: 25px;
    }
    &--bottom {
      padding: 20px 0;
    }
  }

  .footer__logo {
    margin: 0 auto;
  }

  .footer__contacts {
    width: 100%;
    max-width: 450px;
    margin: 20px auto 0;
  }

  .footer__address, .footer__phone {
    display: inline-block;
    font-size: 15px;
  }

  .footer__address {
    margin-right: 15px;
  }

  .footer__nav-pages, .footer__nav-category {
    max-width: 450px;
    margin: 15px auto 0;
  }

  .footer__box--bottom .footer__inner {
    display: block;
  }


  .footer__box--bottom .footer__copyright {
    text-align: center;
  }

  .footer__box--bottom .footer__social {
    margin: 20px auto 0;
  }

  .footer__box--bottom .footer__text-wrap {
    display: block;
    margin: 0;
    margin-top: 10px;
    text-align: center;
  }

  .footer__box--bottom .footer__text {
    display: inline-block;
    margin: 0;
    margin-bottom: 15px;
    padding: 0 15px;

    &:last-child{
      margin-bottom: 0;
    }
  }
}
